import React, { useEffect } from "react";
import {
    Dialog,
    DialogTitle,
    Typography,
    CircularProgress,
} from "@material-ui/core";
import { GoogleLogin } from "react-google-login";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";

import { createSession, showNotification } from "../redux/actions";
import {
    trackContinueWithGoogle,
    trackContinueWithGoogleFail,
    trackContinueWithGoogleSuccess,
} from "../util/analytics";

const useStyles = makeStyles(() => ({
    root: {
        padding: 24,
    },
    title: {
        textAlign: "center",
        marginTop: 16,
        padding: 0,
    },
    note: {
        fontSize: 16,
        color: "#606060",
        marginTop: 16,
        maxWidth: 300,
        textAlign: "center",
    },
    logo: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        marginTop: 8,
    },
    logoImage: {
        height: 40,
        width: "auto",
        marginRight: 8,
    },
    logoText: {
        fontFamily: "IBM Plex Sans",
        fontSize: 28,
        fontWeight: 600,
        verticalAlign: "middle",
    },
    container: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    },
    login: {
        marginTop: 24,
    },
    progress: {
        width: 40,
        height: 40,
    },
}));

const Login = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const { loading, payload } = useSelector((state) => state.session);

    useEffect(() => {
        if (payload) {
            history.push("/feed");
        }
    }, [history, payload]);

    const handleContinueWithGoogle = () => {
        trackContinueWithGoogle();
    };

    const handleClose = () => {};

    // https://dev.to/sivaneshs/add-google-login-to-your-react-apps-in-10-mins-4del
    const handleSuccess = (response) => {
        trackContinueWithGoogleSuccess();
        let refreshTiming =
            (response.tokenObj.expires_in || 3600 - 5 * 60) * 1000;

        const refreshToken = async () => {
            const newAuthResponse = await response.reloadAuthResponse();
            refreshTiming =
                (newAuthResponse.expires_in ||
                    newAuthResponse.expires_in ||
                    3600 - 5 * 60) * 1000;

            dispatch(createSession(response.tokenId));
            setTimeout(refreshToken, refreshTiming);
        };

        dispatch(createSession(response.tokenId));
        setTimeout(refreshToken, refreshTiming);
    };

    const handleFailure = (event) => {
        console.log(event);
        trackContinueWithGoogleFail();
        dispatch(showNotification("Failed to authorize", "error"));
    };

    return (
        <Dialog onClose={handleClose} open={true}>
            <div className={classes.root}>
                <DialogTitle className={classes.title}>
                    Please login to
                </DialogTitle>
                <div className={classes.logo}>
                    <img
                        className={classes.logoImage}
                        src={`${process.env.PUBLIC_URL}/assets/images/logo.png`}
                        alt="Blogpod"
                    />
                    <Typography className={classes.logoText}>
                        blogpod
                    </Typography>
                </div>
                <p component="body1" className={classes.note}>
                    Your privacy is important to us. We will not post anything
                    anywhere.
                </p>
                <div className={classes.container}>
                    {!loading && (
                        <div onClick={handleContinueWithGoogle}>
                            <GoogleLogin
                                className={classes.login}
                                clientId={
                                    process.env.REACT_APP_GOOGLE_OAUTH_CLIENT_ID
                                }
                                buttonText="Continue with Google"
                                onSuccess={handleSuccess}
                                onFailure={handleFailure}
                                cookiePolicy="single_host_origin"
                                isSignedIn={false}
                            />
                        </div>
                    )}
                    {loading && (
                        <CircularProgress className={classes.progress} />
                    )}
                </div>
            </div>
        </Dialog>
    );
};

export default Login;
